<template>
  <div class="top">
    <van-form @submit="onSubmit" @failed="failed">
      <van-field readonly clickable name="whomInsure" :value="form.whomInsure" label="为谁投保" placeholder=""
        :rules="[{ required: true, message: '请选择为谁投保' }]" @click="showShomInsure = true">
        <template #button>
          <span class="chioce" v-if="!form.whomInsure">请选择 </span>
          <van-icon v-if="!form.whomInsure" color="#4ca2fe" style="vertical-align: middle" size="20px" name="arrow" />
          <van-icon v-if="form.whomInsure" style="vertical-align: middle" size="20px" name="arrow" />
        </template>
      </van-field>
      <van-popup v-model="showShomInsure" position="bottom">
        <van-picker show-toolbar :columns="columnsShowShomInsure" @confirm="onConfirm"
          @cancel="showShomInsure = false" />
      </van-popup>

      <van-field readonly clickable name="ChoosePlan" :value="form.ChoosePlan" label="选择方案" placeholder=""
        :rules="[{ required: true, message: '请选择方案' }]" @click="showChoosePlan = true">
        <template #button>
          <span class="chioce" v-if="!form.ChoosePlan">请选择 </span>
          <van-icon v-if="!form.ChoosePlan" color="#4ca2fe" style="vertical-align: middle" size="20px" name="arrow" />
          <van-icon v-if="form.ChoosePlan" style="vertical-align: middle" size="20px" name="arrow" />
        </template>
      </van-field>
      <van-popup v-model="showChoosePlan" position="bottom">
        <van-picker show-toolbar :columns="columnsChoosePlan" @confirm="onConfirmChoosePlan"
          @cancel="showChoosePlan = false" />
      </van-popup>

      <div class="planClass" v-if="getListPlan.damage">
        <div class="planClass_div">
          <div>意外伤害</div>
          <div>{{ getListPlan.damage }}</div>
        </div>
        <div class="planClass_div">
          <div>意外住院</div>
          <div>{{ getListPlan.hospital }}</div>
        </div>
        <div class="planClass_div">
          <div>意外身故</div>
          <div>{{ getListPlan.die }}</div>
        </div>
        <div class="planClass_div">
          <div>保障期间</div>
          <div>{{ getListPlan.securityDuring }}</div>
        </div>
        <div class="planClass_div">
          <div>保单生效日</div>
          <div>{{ getListPlan.effectiveDate }}</div>
        </div>
      </div>

      <van-field v-model="form.userName" name="userName" maxlength="20" label="姓名" placeholder="请输入姓名" :rules="[
        { required: true, message: '请输入姓名' },
        { validator: userNameValidator, message: '姓名格式不正确' },
      ]">
      </van-field>

      <van-field v-model="form.idCard" name="idCard" label="身份证号码" maxlength="18" placeholder="请输入身份证号码" :rules="[
        { required: true, message: '请输入身份证号码' },
        { validator: idCardValidator, message: '身份证号码格式不正确' },
      ]" />
      <van-field readonly clickable name="dateStartTime" :value="form.dateStartTime" label="证件有效起期" placeholder=""
        @click="DateShowPickerStart = true" :rules="[{ required: true, message: '请选择身份证有效期' }]">
        <!-- 请选择 -->
        <template #button>
          <span class="chioce" v-if="!form.dateStartTime">请选择 </span>
          <van-icon v-if="!form.dateStartTime" color="#4ca2fe" style="vertical-align: middle" size="20px"
            name="arrow" />
          <van-icon v-if="form.dateStartTime" style="vertical-align: middle" size="20px" name="arrow" />
        </template>
      </van-field>
      <!-- 时间选择器弹框 -->
      <van-popup v-model="DateShowPickerStart" position="bottom">
        <van-datetime-picker v-model="currentDateStart" type="date" title="选择年月日" :min-date="minDate"
          :max-date="maxDate" @confirm="DateOnConfirm" @cancel="DateShowPickerStart = false" />
      </van-popup>

      <van-field readonly clickable name="dateEndTime" :value="form.dateEndTime" label="证件有效止期" placeholder=""
        @click="DateShowPickerEnd = true" :rules="[{ required: true, message: '请选择身份证有效止期' }]">
        <!-- 点击长期 -->
        <template #button>
          <div class="dateEndTime">
            <span class="chioce" v-if="!form.dateEndTime">请选择 </span>
            <van-icon v-if="!form.dateEndTime" color="#4ca2fe" style="vertical-align: bottom" size="20px"
              name="arrow" />
            <van-icon v-if="form.dateEndTime" style="vertical-align: middle" size="20px" name="arrow" />
            <div class="longTime" @click.stop="longTime">长期</div>
          </div>
        </template>
      </van-field>
      <!-- 时间选择器弹框 -->
      <van-popup v-model="DateShowPickerEnd" position="bottom">
        <van-datetime-picker v-model="currentDateStart" type="date" title="选择年月日" :min-date="cardDate"
          :max-date="maxDate" @confirm="DateOnConfirmEnd" @cancel="DateShowPickerEnd = false" />
      </van-popup>

      <!-- 手机号码 -->
      <van-field v-model="form.phone" name="phone" label="手机号码" placeholder="请输入手机号码" @change="formPhoenChange"
        type="digit" maxlength="11" :rules="[
          { required: true, message: '请输入手机号码' },
          { validator: phoneValidator, message: '手机号码格式不正确' },
        ]" />

      <van-field name="bartender" label="有无社保">
        <template #button>
          <div class="bartender">
            <div :class="[
              form.haveBartender == 1
                ? 'bartender_div1'
                : 'bartender_div1_err',
            ]" @click="haveBartender(1)">
              <img v-if="form.haveBartender == 1" src="@/assets/image/check.png" alt="" />
              有
            </div>
            <div :class="[
              form.haveBartender == 2
                ? 'bartender_div2'
                : 'bartender_div2_err',
            ]" @click="haveBartender(2)">
              <img v-if="form.haveBartender == 2" src="@/assets/image/check.png" alt="" />无
            </div>
          </div>
        </template>
      </van-field>

      <van-field readonly clickable name="professionalType" :value="form.professionalType" label="职业类型" placeholder=""
        @click="showprofessionalType = true" :rules="[{ required: true, message: '请选择职业类型' }]">
        <template #button>
          <span class="chioce" v-if="!form.professionalType">请选择 </span>
          <van-icon v-if="!form.professionalType" color="#4ca2fe" style="vertical-align: middle" size="20px"
            name="arrow" />
          <van-icon v-if="form.professionalType" style="vertical-align: middle" size="20px" name="arrow" />
        </template>
      </van-field>
      <van-popup v-model="showprofessionalType" position="bottom">
        <van-picker show-toolbar :columns="columnsShowprofessionalType" @confirm="onConfirmprofessionalType"
          @cancel="showprofessionalType = false" />
      </van-popup>

      <div style="margin: 16px">
        <van-button round block type="info" native-type="submit">提交</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import util from "@/components/_utils";
import isIdcard from "../../../../views/card/student-card/views/whiteList/JS/index";
export default {
  name: "",
  components: {},

  data() {
    return {
      //   日期弹框
      cardDate: new Date(+new Date() + 86400000),
      DateShowPickerStart: false,
      DateShowPickerEnd: false,
      minDate: new Date(1900, 1, 1),
      maxDate: new Date(2100, 1, 1),
      currentDateStart: "",
      // 为谁投保弹框
      showShomInsure: false,
      columnsShowShomInsure: ["父母", "子女"],
      // 选择方案
      showChoosePlan: false,
      columnsChoosePlan: [],
      // 职业类型
      showprofessionalType: false,
      columnsShowprofessionalType: ["教师", "IT", ""],

      getList: [
        {
          id: 1,
          value: "方案一",
          damage: "10万元",
          hospital: "10万元",
          die: "10万元",
          securityDuring: "12个月",
          effectiveDate: "2022-12-22",
        },
        {
          id: 2,
          value: "方案二",
          damage: "20万元",
          hospital: "20万元",
          die: "20万元",
          securityDuring: "12个月",
          effectiveDate: "2022-12-22",
        },
        {
          id: 3,
          value: "方案3",
          damage: "30万元",
          hospital: "30万元",
          die: "30万元",
          securityDuring: "12个月",
          effectiveDate: "2022-12-22",
        },
      ],
      // 获取选择方案下面的内容
      getListPlan: [],
      // 表单值
      form: {
        whomInsure: "",
        ChoosePlan: "",
        userName: "",
        idCard: "",
        dateStartTime: "",
        dateEndTime: "",
        phone: "",
        haveBartender: "1",
        professionalType: "",
      },
      InformationFormJSON: JSON.parse(sessionStorage.getItem('InformationForm_confirm'))
    };
  },

  created() {
    // 如果有缓存就反显
    // if (this.InformationFormJSON) {
    //   this.form = this.InformationFormJSON;

    //   // 找到 匹配的方案
    //   this.getListPlan = this.getList.find((e) => {
    //     return e.value == this.InformationFormJSON.ChoosePlan;
    //   });
    // }
    //   获取当前日期
    let data = new Date();
    let year = data.getFullYear();
    let month = data.getMonth();
    let day = data.getDate();
    // 将现在日期赋值给日期选择器上
    this.currentDateStart = new Date(year, month, day);
  },
  mounted() {
    // 给选择方案赋值
    this.columnsChoosePlan = this.getList.map((e) => {
      return e.value;
    });
  },
  methods: {
    // 判断有没有设社保
    haveBartender(value) {
      this.form.haveBartender = value;
    },
    // 点击长期有效
    longTime() {
      this.form.dateEndTime = "长期有效";
    },
    // 改变电话号码的判断
    formPhoenChange() {
      return this.phoneValidator();
    },
    // 职业类型弹框的确认
    onConfirmprofessionalType(value, index) {
      this.form.professionalType = value;
      this.showprofessionalType = false;
    },
    // 选择身份证弹框中确认
    DateOnConfirmEnd(time) {
      let setMonth = time.getMonth() + 1;
      if (setMonth >= 1 && setMonth <= 9) {
        setMonth = "0" + setMonth;
      }
      let day = time.getDate();
      if (day < 10) {
        day = "0" + day;
      }
      this.form.dateEndTime = `${time.getFullYear()}-${setMonth}-${day}`;
      this.DateShowPickerEnd = false;
    },
    DateOnConfirm(time) {
      let setMonth = time.getMonth() + 1;
      if (setMonth >= 1 && setMonth <= 9) {
        setMonth = "0" + setMonth;
      }
      let day = time.getDate();
      if (day < 10) {
        day = "0" + day;
      }
      this.form.dateStartTime = `${time.getFullYear()}-${setMonth}-${day}`;
      this.DateShowPickerStart = false;
    },
    // 姓名校验
    userNameValidator() {
      return util.checkName(this.form.userName);
    },
    // 身份证格式的验证
    idCardValidator() {
      return isIdcard(this.form.idCard);
    },
    // 手机号格式的验证
    phoneValidator() {
      return /^1[3|4|5|6|7|8|9][0-9]\d{8}$/.test(this.form.phone);
    },
    // 提交失败
    failed(values) {
      this.$toast(
        values.errors[0].message
      )
      console.log('va', values);

    },
    // 表单确认
    onSubmit(values) {
      console.log("submit", values);
      console.log("this。form", this.form);
      this.$router.push({
        path: "/card/HealthInformation",
      });
      // 存JSON
      if (this.InformationFormJSON) {
        this.InformationFormJSON.push(this.form);
        sessionStorage.setItem(
          "InformationForm",
          JSON.stringify(this.InformationFormJSON)
        );
      } else {
        let arrList = [];
        arrList.push(this.form);
        sessionStorage.setItem("InformationForm", JSON.stringify(arrList));
      }
    },
    // 点击为谁投保的确认
    onConfirm(value, index) {
      console.log("选择为谁投保", value, index);
      // 给为谁投保后面赋值
      this.form.whomInsure = value;
      this.showShomInsure = false;
    },
    // 选择方案
    onConfirmChoosePlan(value, index) {
      console.log("选择方案", value, index);
      this.form.ChoosePlan = value;
      this.showChoosePlan = false;
      // 找到 匹配的方案
      this.getListPlan = this.getList.find((e) => {
        return e.id == index + 1;
      });
      console.log(this.getListPlan);
    },
  },
  watch: {},
  computed: {},
};
</script>

<style lang='less' scoped>
.top {
  padding-right: 1vh;
}

.chioce {
  color: #4ca2fe;
}

/deep/ .van-field__control {
  text-align: right;
}

.planClass {
  padding: 3vh;
  display: flex;
  flex-direction: column;
  background: #ffffff;
  box-shadow: 0px 0px 6px 1px rgba(159, 159, 159, 0.16);
  border-radius: 8px 8px 8px 8px;
  opacity: 1;
}

.planClass_div {
  margin-top: 2vh;
  display: flex;
  justify-content: space-between;
}

/deep/ .van-field__label {
  padding-left: 1vh;

  font-weight: 550;
  width: 110px;
}

.dateEndTime {
  display: flex;
  justify-content: center;
  align-items: center;
}

.longTime {
  color: #4ca2fe;
  border-radius: 5px;
  border: #4ca2fe 1px solid;
  padding: 2px;
  width: 50px;
  height: 28px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: small;
}

/deep/ .van-field__error-message {
  text-align: right;
}

/deep/ .van-field--error .van-field__control,
.van-field--error .van-field__control::placeholder {
  color: #b8b7b7;
}

.bartender {
  display: flex;
}

.bartender_div1 {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  width: 34px;
  height: 35px;
  border-radius: 4px 4px 4px 4px;
  opacity: 1;
  border: 1px solid #4ca2fe;
  color: #4ca2fe;
  margin-right: 2vh;

  img {
    position: absolute;
    top: 0;
    right: 0;
    width: 20px;
    height: 20px;
  }
}

.bartender_div1_err {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  width: 34px;
  height: 35px;
  border-radius: 4px 4px 4px 4px;
  opacity: 1;
  border: 1px solid #9ca1a6;
  // color: #4ca2fe;
  margin-right: 2vh;
}

.bartender_div2_err {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  width: 34px;
  height: 35px;
  border-radius: 4px 4px 4px 4px;
  opacity: 1;
  border: 1px solid #9ca1a6;
  // color: #4ca2fe;
}

.bartender_div2 {
  position: relative;
  color: #4ca2fe;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 34px;
  height: 35px;
  border-radius: 4px 4px 4px 4px;
  opacity: 1;
  border: 1px solid #4ca2fe;

  img {
    position: absolute;
    top: 0;
    right: 0;
    width: 20px;
    height: 20px;
  }
}
</style>
